import { useState } from "react";
import DatePicker, { setDefaultLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import fi from "date-fns/locale/fi";
import PrioritySelect from "./PrioritySelect.js";
import ListSelect from "./ListSelect.js";
const axios = require("axios");

export default function TaskForm(props) {
  const [name, setName] = useState("");
  const [desc, setDesc] = useState("");
  const [datetime, setDatetime] = useState(new Date());
  const [priority, setPriority] = useState("1");
  const [chosenList, setChosenList] = useState(props.list);
  //const [errors, setErrors] = useState({})

  const handleFormSubmit = async (event) => {
    event.preventDefault();
    // "" is 0 in database so change it to null before posting
    let listId = chosenList.id === "" ? null : chosenList.id;
    if (name !== "") {
    await axios
      .post(
        `https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=${process.env.REACT_APP_APIKEY}`,
        {
          user_id: 1,
          name: name,
          description: desc,
          priority: priority,
          deadline: datetime.toISOString(),
          list_id: listId,
        }
      )
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
    }  
    await props.reFetchTasks();
    await props.setAppState("tasks");
  };

  return (
    <form class="taskform" onSubmit={handleFormSubmit}>
      <table>
        <tr>
          <td>
            <label>Task name:</label>
          </td>
          <td>
            <input
              className="forminput"
              type="text"
              name="name"
              onChange={(event) => setName(event.target.value)}
            ></input>
          </td>
        </tr>
        <tr>
          <td>
            <label>Deadline:</label>
          </td>
          <td>
            <DatePicker
              className="forminput"
              selected={datetime}
              onChange={(date) => setDatetime(date)}
              locale={fi}
              timeFormat="p"
              dateFormat="Pp"
              showTimeInput
            />
          </td>
        </tr>
        <tr>
          <td>
            <label>Desc:</label>
          </td>
          <td>
            <textarea
              rows="4"
              cols="22"
              type="text"
              name="desc"
              onChange={(event) => setDesc(event.target.value)}
            ></textarea>
          </td>
        </tr>
        <tr>
          <td>
            <label>Priority:</label>
          </td>
          <td>
            <PrioritySelect
              onChange={(event) => setPriority(event.target.value)}
            ></PrioritySelect>
          </td>
        </tr>
        <tr>
          <label>List:</label>
          <td>
            <ListSelect
              onChange={(event) =>
                setChosenList(JSON.parse(event.target.value))
              }
              list={props.list}
              lists={props.lists}
              setLists={props.setLists}
              selectClass="forminput"
            ></ListSelect>
          </td>
        </tr>
        <input className="basicButton" type="submit" value="Save"></input>
      </table>
    </form>
  );
}
