import RemoveTask from "./RemoveTask.js";
import { BiBadgeCheck, BiBadge } from "react-icons/bi";
const axios = require("axios");

const ISOStringToDatetime = (ISOString) => {
  const date = new Date(ISOString);
  const weekDay = date.toUTCString().split(" ")[0];
  return (
    date.getUTCHours() +
    ":" +
    date.getMinutes() +
    " " +
    weekDay +
    " " +
    date.getDate() +
    "." +
    (date.getMonth() + 1) +
    " " +
    date.getFullYear()
  );
};

export default function List(props) {
  const clickHandler = (id) => () => {
    changeCheckState(id);
  };

  const changeCheckState = async (id) => {
    await axios
      .post(
        `https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks/check/${id}?apikey=${process.env.REACT_APP_APIKEY}`
      )
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
    await props.reFetchTasks();
  };

  return (
    <div>
      <div class="center">
        {props.data.map((data) => (
          <div class="list-itembase">
            <button class="menu-trigger task">
              <div>{data.name}</div>
              <div>{ISOStringToDatetime(data.deadline)}</div>
              <div>{data.description}</div>
              <div>priority: {data.priority}</div>
              <div class="syle-task-buttons">
                <button
                  style={{ maxHeight: "40px", maxWidth: "48px" }}
                  onClick={clickHandler(data.id)}
                >
                  {data.is_done ? (
                    <BiBadgeCheck size={32} />
                  ) : (
                    <BiBadge size={32} />
                  )}
                </button>

                <RemoveTask
                  reFetchTasks={props.reFetchTasks}
                  task={data}
                ></RemoveTask>
              </div>
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}
