import { useState } from "react";
const axios = require("axios");

export default function CreateList(props) {
  const [list, setList] = useState("");

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (list !== "") {
      await axios
        .post(`https://tamk-4a00ez62-3001-group07.herokuapp.com/api/lists?apikey=${process.env.REACT_APP_APIKEY}`, {
          list_name: list,
        })
        .then(function (response) {
          console.log(response);
        })
        .catch(function (error) {
          console.log(error);
        });
      setList("");
      props.reFetchLists();
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit} className="createlist">
        <input className="basicButton" type="submit" value="Add list" />
        <input
          className="listInput"
          type="text"
          name="list"
          placeholder="List name"
          value={list}
          onChange={(event) => setList(event.target.value)}
        />
      </form>
    </div>
  );
}
