export default function PrioritySelect(props) {
  return (
    <select className="forminput" onChange={props.onChange}>
      <option value="1">Priority 1 (Very important)</option>
      <option value="2">Priority 2</option>
      <option value="3">Priority 3</option>
      <option value="4">Priority 4</option>
      <option value="5">Priority 5 (Not that important)</option>
    </select>
  );
}

