import { useState } from "react";
import Button from "./Button.js";

export default function AddButton(props) {
  const handleClick = () => {
    props.appState === "tasks"
      ? props.setAppState("taskForm")
      : props.setAppState("tasks");
  };

  return (
    <div className="addButton">
      <Button
        className="addButton"
        value={props.appState === "taskForm" ? "Close" : "Add task"}
        onClick={handleClick}
      ></Button>
    </div>
  );
}
