import axios from "axios";
import { BsFillTrashFill } from "react-icons/bs";
import { useState } from "react";
import RemoveConfirmation from "./RemoveConfirmation.js";

export default function List(props) {
  const [removeState, setRemoveState] = useState(false);

  const removeTaskConfirmation = () => {
    removeState === false ? setRemoveState(true) : setRemoveState(false);
  };

  return (
    <div className="deleteTask">
      <button className="deleteButton" onClick={removeTaskConfirmation}>
        <BsFillTrashFill size={32} />
      </button>
      {removeState === true ? (
        <RemoveConfirmation
          reFetchTasks={props.reFetchTasks}
          task={props.task}
          setRemoveState={setRemoveState}
        />
      ) : (
        ""
      )}
    </div>
  );
}
