export default function ListSelect(props) {
  return (
    <select className={`${props.selectClass}`} onChange={props.onChange}>
      <option value={props.list.id}>{props.list.list_name}</option>
      {props.list.list_name === "no list" ? "" : <option value={JSON.stringify({"id": "", "list_name": "no list"})}>no list</option>}
      {props.lists.map((list) => (
        props.list.id !== list.id ? <option value={JSON.stringify(list)}>{list.list_name}</option> : ""
      ))}
    </select>
  );
}
