import { AiFillCaretDown, AiFillCaretUp } from "react-icons/ai";

export default function SortInfo(props) {
  return (
    <div>
      <div className="sortedBy">
        <div>List: {props.list.list_name}</div>
        <div>
          Sorted by: {props.sort}
          {"  "}
          {props.sortDirection === "+" ? (
            <AiFillCaretUp />
          ) : (
            <AiFillCaretDown />
          )}
        </div>
      </div>
    </div>
  );
}
