import axios from "axios";
import { AiOutlineCheck, AiOutlineClose } from "react-icons/ai";

export default function List(props) {
  const removeTask = async () => {
    await axios.delete(
      "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks/" +
      props.task.id +
      "?apikey="
      + process.env.REACT_APP_APIKEY
    );
    await props.reFetchTasks();
    props.setRemoveState(false)
  };

  const reject = () => {
    props.setRemoveState(false);
  };

  return (
    <div className="removeConfirmation">
      <p>Are you sure?</p>
      <div class="syle-taskRemoveButtons">
        <button className="deleteButton" onClick={removeTask}>
          <AiOutlineCheck size={32} />
        </button>
        <button className="deleteButton" onClick={reject}>
          <AiOutlineClose size={32} />
        </button>
      </div>
    </div>
  );
}
