import React, { useRef } from "react";
import { setDefaultLocale } from "react-datepicker";
import { useDetectOutsideClick } from "./useDetectOutsideClick";
import { AiFillCaretDown, AiFillCaretUp } from "react-icons/ai";
import ListSelect from "./ListSelect.js";
const axios = require("axios")

export default function DropdownMenu(props) {
  const dropdownRef = useRef(null);
  const [isActive, setIsActive] = useDetectOutsideClick(dropdownRef, false);
  const onClick = () => setIsActive(!isActive);

  const handleSort = async (direction, sort, list) => {
    await props.setSort(sort)
    console.log("https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=" + process.env.REACT_APP_APIKEY + "&sort=" + direction + sort + "&list=" + list.id)
    const response = await axios.get("https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=" + process.env.REACT_APP_APIKEY + "&sort=" + direction + sort + "&list=" + list.id)
    props.setData(response.data)
    console.log(response.data)
  }

  const getSortDirectionIcon = () => {
    return props.sortDirection === "+" ? <AiFillCaretUp/>  : <AiFillCaretDown/>
  }

  const handleListChange = async (event) => {
    const list = await JSON.parse(event.target.value)
    props.setList(list)
    handleSort(props.sortDirection, props.sort, list)
  }

  const handleIconClick = () => {
    const direction = props.sortDirection === "+" ? "-" : "+"
    console.log(direction)
    props.setSortDirection(direction)
    handleSort(direction, props.sort, props.list)
  }

  return (
    <div className="menu-container">
      <button onClick={onClick} className="sortButton">
        <img
          src="https://cdn2.iconfinder.com/data/icons/user-interface-179/100/IF_Creator_Icon_05-16-512.png"
          alt="adjust sort"
        />
      </button>
      <nav
        ref={dropdownRef}
        className={`menu ${isActive ? "active" : "inactive"}`}
      >
        <ul>
          <li className="sortItem">
            <ListSelect selectClass="listSelect" list={props.list} lists={props.lists} onChange={e => handleListChange(e)}></ListSelect>
            <button className="textButton sortDirectionButton" onClick={handleIconClick} >{getSortDirectionIcon()}</button>
          </li>
          <li className="sortItem">
            <button className={`textButton sortTypeButton ${props.sort.includes("name") ? "selected" : ""}`} onClick={() => {handleSort(props.sortDirection, "name", props.list)}}>name</button>
          </li>
          <li className="sortItem">
            <button className={`textButton sortTypeButton ${props.sort.includes("deadline") ? "selected" : ""}`} onClick={() => {handleSort(props.sortDirection, "deadline", props.list)}}>deadline</button>
          </li>
          <li className="sortItem">
            <button className={`textButton sortTypeButton ${props.sort.includes("priority") ? "selected" : ""}`} onClick={() => {handleSort(props.sortDirection, "priority", props.list)}}>priority</button>
          </li>
        </ul>
      </nav>
    </div>
  );
}
