import "./App.scss";
import AddButton from "./Components/AddButton.js";
import SearchBar from "./Components/SearchBar.js";
import CreateList from "./Components/CreateList.js";
import DropdownMenu from "./Components/DropdownMenu.js";
import SortInfo from "./Components/SortInfo.js";
import List from "./Components/List.js";
import { useState, useEffect } from "react";
import axios from "axios";
import "./syle.css";
import TaskForm from "./Components/TaskForm.js";
require("dotenv").config();

function App() {
  const [data, setData] = useState([]);
  const [lists, setLists] = useState([]);
  const [sort, setSort] = useState("deadline");
  const [sortDirection, setSortDirection] = useState("+");
  const [list, setList] = useState({ id: "", list_name: "no list" });

  // What view should be seen. Mostly used for mobile
  // default-view: 'tasks', taskform-view: 'taskForm'
  const [appState, setAppState] = useState("tasks");

  useEffect(() => {
    async function fetchData() {
      console.log(
        "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=" +
          process.env.REACT_APP_APIKEY +
          "&sort=" +
          sortDirection +
          sort +
          "&list=" +
          list.id
      );
      const tasks = await axios.get(
        "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=" +
          process.env.REACT_APP_APIKEY +
          "&sort=" +
          sortDirection +
          sort +
          "&list=" +
          list.id
      );
      setData(tasks.data);
      const lists = await axios.get(
        "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/lists?apikey=" +
          process.env.REACT_APP_APIKEY
      );
      setLists(lists.data);
    }
    fetchData();
  }, []);

  const reFetchTasks = async () => {
    const tasks = await axios.get(
      "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/tasks?apikey=" +
        process.env.REACT_APP_APIKEY +
        "&sort=" +
        sortDirection +
        sort +
        "&list=" +
        list.id
    );
    setData(tasks.data);
  };

  const reFetchLists = async () => {
    const lists = await axios.get(
      "https://tamk-4a00ez62-3001-group07.herokuapp.com/api/lists?apikey=" +
        process.env.REACT_APP_APIKEY
    );
    setLists(lists.data);
  };

  return (
    <div class="syle-body">
      <div class="syle-topbar">
        <SortInfo list={list} sort={sort} sortDirection={sortDirection} />
        <div class="syle-search-sort">
          <SearchBar />
          <DropdownMenu
            reFetchTasks={reFetchTasks}
            reFetchLists={reFetchLists}
            list={list}
            setList={setList}
            lists={lists}
            sort={sort}
            setSort={setSort}
            setData={setData}
            sortDirection={sortDirection}
            setSortDirection={setSortDirection}
          />
        </div>
      </div>
      <div class="syle-listbody">
        <div class="syle-list">
          {appState === "taskForm" ? (
            <TaskForm
              reFetchTasks={reFetchTasks}
              list={list}
              setAppState={setAppState}
              lists={lists}
              setLists={setLists}
              setList={setList}
              sortDirection={sortDirection}
              setSortDirection={setSortDirection}
              setData={setData}
              sort={sort}
            ></TaskForm>
          ) : (
            <List reFetchTasks={reFetchTasks} data={data} />
          )}
        </div>
        <div class="syle-footer">
          <AddButton
            list={list}
            lists={lists}
            setLists={setLists}
            sortDirection={sortDirection}
            setSortDirection={setSortDirection}
            setData={setData}
            sort={sort}
            appState={appState}
            setAppState={setAppState}
          />
          <CreateList reFetchLists={reFetchLists} />
        </div>
      </div>
    </div>
  );
}

export default App;
